  cursor: pointer;
}

.boton:hover {
  background-color: #619800;
}

.boton-naranja-block {
  background-color: #E08709;
  width: 100%;
  text-align: center;
  color: #FFFFFF;
  padding: 1rem 2rem;
  font-weight: 700;
  border: none;
  display: block;
  cursor: pointer;
}

.boton-naranja-block:hover {
  background-color: #c77808;
}

.boton-rojo-block {
  background-color: #b71c1c;
  width: 100%;
  text-align: center;
  color: #FFFFFF;
  padding: 1rem 2rem;
  font-weight: 700;
  border: none;
  display: block;
  cursor: pointer;
}

.boton-rojo-block:hover {
  background-color: #a11919;
}

.imagen-peque {
  width: 25rem;
}

.contenedor-boton-login {
  display: flex;
  justify-content: center;
  display: block;
}

.contenedor-boton-login button {
  font-size: 1.5rem;
  display: block;
}

html {
  font-size: 62.5%;
  box-sizing: border-box;
}

*, *:before, *:after {
  box-sizing: inherit;
}

body {
  font-family: "Lato", sans-serif;
  font-size: 1.6rem;
  line-height: 1.8;
}

p {
  color: #000000;
  font-size: 2rem;
}

a {
  text-decoration: none;
}

img,
picture {
  width: 100%;
  display: block;
}

h1, h2, h3 {
  font-family: "Lato", sans-serif;
  margin: 2.5rem 0;
  font-weight: 300;
  text-align: center;
}

h1 {
  font-size: 3.8rem;
}

h2 {
  font-size: 3.4rem;
}

h3 {
  font-size: 3rem;
}

h4 {
  font-size: 2.6rem;
}

.dark-mode {
  background-color: #1a1a1a;
}

.dark-mode p, .dark-mode label {
  color: #FFFFFF;
}

.dark-mode a, .dark-mode h1, .dark-mode h2, .dark-mode h3, .dark-mode h4, .dark-mode legend {
  color: #e1e1e1;
}

.dark-mode .propiedades__propiedad {
  background-color: #333333;
  border: none;
}

.dark-mode .propiedades__propiedad .propiedades__info img {
  filter: invert(1);
}

.dark-mode .anuncio img {
  filter: invert(1);
}

.dark-mode .formulario textarea, .dark-mode .formulario select, .dark-mode .formulario input:not([type=radio]) {
  background-color: #333333;
  color: #FFFFFF;
  border: 1px solid #333333;
}

/** Media Queries **/
.header {
  height: 17rem;
  background-color: #333333;
  padding: 3rem 0;
  width: 100%;
  transition: height 0.1s linear;
}

.header.inicio.nav-activa {
  height: 50rem;
}

.header.nav-activa {
  height: 36rem;
}

@media (min-width: 480px) {
  .header {
    height: 22rem;
  }
  .header.nav-activa {
    height: 40rem;
  }
}
@media (min-width: 768px) {
  .header {
    height: 17rem;
  }
}
.header.inicio {
  background-image: url("/build/img/header.webp");
  background-position: center center;
  background-size: cover;
  height: 40rem;
}

@media (min-width: 768px) {
  .header.inicio {
    height: 60rem;
  }
}
.header__contenido {
  display: flex;
  flex-direction: column;
  position: relative;
}

@media (min-width: 768px) {
  .header__contenido {
    align-items: center;
  }
}
@media (min-width: 768px) {
  .header .logo {
    width: 35rem;
  }
}
.barras {
  display: block;
  margin-top: 1rem;
  height: 4rem;
}

@media (min-width: 768px) {
  .barras {
    display: none;
    height: 4rem;
  }
}
.barras:hover {
  cursor: pointer;
}

.navegacion {
  visibility: hidden;
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 1rem;
  opacity: 0;
  transition: visibility, opacity 0.3s ease-in-out;
}

@media (min-width: 768px) {
  .navegacion {
    visibility: visible;
    flex-direction: row;
    gap: 5rem;
    font-size: 1.8rem;
    margin-top: 2rem;
    opacity: 1;
  }
}
.navegacion a {
  color: #FFFFFF;
}

.navegacion :hover {
  color: #71B100;
}

.navegacion.nav-activa {
  visibility: visible;
  opacity: 1;
}

.header__subtitulos {
  margin-top: -7rem;
  transition: margin-top 0.1s linear;
}

.header__subtitulos.nav-activa {
  margin-top: 2rem;
}

@media (min-width: 768px) {
  .header__subtitulos {
    margin-top: 30rem;
  }
}
.header__subtitulos .header__subtitulo {
  color: #FFFFFF;
  text-align: left;
  font-weight: 700;
  margin: 0;
  font-size: 2.5rem;
}

.header__subtitulos .header__subtitulo:nth-child(2) {
  font-size: 2rem;
}

@media (min-width: 768px) {
  .header__subtitulos .header__subtitulo {
    font-size: 4rem;
  }
  .header__subtitulos .header__subtitulo:nth-child(2) {
    font-size: 3.5rem;
  }
}
.dark-mode-boton {
  height: 3rem;
  position: relative;
  right: 0;
  width: auto;
  filter: invert(1);
  top: -0.1rem;
  cursor: pointer;
}

@media (min-width: 768px) {
  .dark-mode-boton {
    position: absolute;
    height: 5rem;
  }
}
.dark-mode-boton:hover {
  filter: invert(0.5);
}

.footer {
  background-color: #333333;
  padding-top: 2rem;
  padding-bottom: 2rem;
  margin-top: 3rem;
}

@media (min-width: 768px) {
  .footer {
